.products {
  &__container {
    padding-top: 85px;
    padding-bottom: 120px;
  }

  h2 {
    margin: 0;
    margin-bottom: 49px;
    font-size: 28px;
    font-weight: 600;
    line-height: 42px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 260px);
    gap: 30px;
    padding: 0 15px;
  }

  &__item {
    position: relative;
    height: 386px;
    box-shadow: 0px 4px 35px rgba(1, 28, 64, 0.15);

    overflow: hidden;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: cover;
      z-index: -1;
    }

    .products__content {
      display: flex;
      flex-direction: column;
      height: 100%;
      margin-top: 307px;
      padding: 28px 25px 35px;
      color: $color-default-white;
      background-color: transparent;
    }

    h3 {
      margin: 0;
      margin-bottom: 30px;
      padding-left: 5px;
      font-size: 18px;
      font-weight: 500;
      line-height: 27px;
    }

    dl {
      overflow: scroll;
    }

    dt {
      font-weight: 500;
    }

    dd {
      margin: 0;
      margin-bottom: 12px;
      color: $color-quartz;
    }

    .products__button {
      align-self: center;
      width: 100%;
      margin-top: auto;
    }

    &:hover {
      img {
        display: none;
      }

      .products__content {
        margin: 0;
        background-color: $color-tangaroa;
      }

      h3 {
        margin-bottom: 15px;
        padding-left: 0;
      }
    }
  }
}
