@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/montserrat-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/montserrat-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../../fonts/montserrat-semibold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/montserrat-bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@media (min-width: 768px) {
  .hidden-not-mobile {
    display: none;
  }
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: absolute;
  width: 100%;
  z-index: 2;
}
.header a {
  color: #f9fbfd;
}
.header a:hover, .header a:focus {
  color: #011c40;
  opacity: 0.6;
}
.header__logo {
  height: 38px;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 35px;
  padding-bottom: 35px;
}

.nav__list {
  display: flex;
  align-items: center;
  gap: 33px;
}
.nav__list a {
  width: 100%;
  height: 100%;
  font-weight: 500;
  font-size: 16px;
}
.nav__list a:active {
  border: 1px solid #101a56;
}

.homescreen {
  position: relative;
}
.homescreen img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.homescreen__container {
  padding: 270px 134px 278px;
  color: #f9fbfd;
}
.homescreen h1 {
  margin: 0;
  margin-bottom: 12px;
  font-size: 54px;
  line-height: 66px;
}
.homescreen p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}

.about__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  padding-top: 120px;
  padding-bottom: 145px;
}
.about h2 {
  margin-top: 56px;
  margin-bottom: 24px;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
}
.about p {
  margin: 0;
  font-size: 16px;
  line-height: 24px;
}

.features {
  position: relative;
}
.features img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.features__container {
  padding-top: 110px;
  padding-bottom: 120px;
}
.features h2 {
  margin: 0;
  margin-bottom: 49px;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  text-align: center;
}
.features__list {
  display: flex;
  gap: 40px;
}
.features__list li {
  width: 360px;
  min-height: 241px;
  padding: 30px;
  background-color: #f9fbfd;
}
.features__list h3 {
  margin: 13px 0 9px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}
.features__list p {
  margin: 0;
}

.products__container {
  padding-top: 85px;
  padding-bottom: 120px;
}
.products h2 {
  margin: 0;
  margin-bottom: 49px;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  text-align: center;
}
.products__list {
  display: grid;
  grid-template-columns: repeat(4, 260px);
  gap: 30px;
  padding: 0 15px;
}
.products__item {
  position: relative;
  height: 386px;
  box-shadow: 0px 4px 35px rgba(1, 28, 64, 0.15);
  overflow: hidden;
}
.products__item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-position: center;
  object-fit: cover;
  z-index: -1;
}
.products__item .products__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin-top: 307px;
  padding: 28px 25px 35px;
  color: #f9fbfd;
  background-color: transparent;
}
.products__item h3 {
  margin: 0;
  margin-bottom: 30px;
  padding-left: 5px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}
.products__item dl {
  overflow: scroll;
}
.products__item dt {
  font-weight: 500;
}
.products__item dd {
  margin: 0;
  margin-bottom: 12px;
  color: #d8d8d9;
}
.products__item .products__button {
  align-self: center;
  width: 100%;
  margin-top: auto;
}
.products__item:hover img {
  display: none;
}
.products__item:hover .products__content {
  margin: 0;
  background-color: #011c40;
}
.products__item:hover h3 {
  margin-bottom: 15px;
  padding-left: 0;
}

.feedback {
  position: relative;
}
.feedback img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.feedback__container {
  display: grid;
  grid-template-columns: 493px 403px;
  gap: 144px;
  padding: 50px 100px;
}
.feedback__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}
.feedback__content h2 {
  margin: 0;
  margin-bottom: 8px;
  font-size: 40px;
  line-height: 60px;
}
.feedback__content p {
  margin: 0;
  font-size: 18px;
  line-height: 27px;
}
.feedback__form {
  width: 100%;
  padding: 43px 50px 50px;
  background-color: #011c40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
  color: #f9fbfd;
}
.feedback__form p {
  margin: 0;
  margin-bottom: 25px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}
.feedback__form form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.feedback__form label {
  display: flex;
  align-items: center;
}
.feedback__form input:not([type=checkbox]) {
  width: 100%;
  padding: 11px 16px;
}
.feedback__form input:not([type=checkbox])::placeholder {
  color: rgba(1, 28, 64, 0.7);
}
.feedback__form input[type=checkbox] {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  appearance: none;
  width: 18px;
  height: 18px;
}
.feedback__form input[type=checkbox]::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  border: 1px solid transparent;
  background-color: #f9fbfd;
  content: "";
}
.feedback__form input[type=checkbox]:hover::before, .feedback__form input[type=checkbox]:focus::before {
  background-color: transparent;
  border-color: #f9fbfd;
}
.feedback__form input[type=checkbox]:checked::after {
  position: absolute;
  top: 6px;
  left: 5px;
  content: "";
  min-width: 7px;
  height: 4px;
  border: 1px solid #011c40;
  border-right: none;
  border-top: none;
  transform: rotate(-45deg);
}
.feedback__form input[type=checkbox]:hover:checked::after {
  border-color: #f9fbfd;
}
.feedback__form span {
  margin-left: 5px;
  font-size: 10px;
  line-height: 13px;
}
.feedback__button {
  margin-top: 12px;
}

.contacts__container {
  display: grid;
  grid-template-columns: 560px 500px;
  gap: 100px;
  padding-top: 120px;
  padding-bottom: 120px;
}
.contacts #map {
  height: 306px;
}
.contacts__content {
  align-self: center;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.contacts__content p {
  margin: 0;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}
.contacts__content a {
  display: flex;
  gap: 10px;
  font-size: 16px;
  line-height: 24px;
}

.footer {
  background-color: #011c40;
}
.footer a {
  color: #f9fbfd;
}
.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}
.footer__nav {
  display: flex;
  gap: 40px;
}
.footer__socials {
  display: flex;
  gap: 9px;
  margin-right: -8px;
}
.footer__socials li {
  width: 37px;
  height: 32px;
}
.footer__socials a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.btn {
  display: inline-flex;
  justify-content: center;
  padding: 10px 20px;
  font-weight: 600;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    color: #f9fbfd;
    background-color: #011c40;
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  color: #f9fbfd;
  background-color: #011c40;
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(51, 68, 130, 0.5);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  display: flex;
  width: 500px;
  padding: 14px 0 10px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    padding: 14px 0 10px 0;
    width: 100%;
  }
}
.custom-toggle:last-of-type {
  padding: 14px 0 58px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle:last-of-type {
    padding: 14px 0 58px 0;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 52px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1.5px solid #2c39f2;
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background-color: rgba(44, 57, 242, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #2c39f2;
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  border: 1.5px solid #2c39f2;
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  background-color: #2c39f2;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
.custom-toggle .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}
.custom-toggle .input-message.is-valid {
  color: #15ab07;
}

